import { __ } from '@wordpress/i18n'
import { PanelBody, SelectControl, TextControl, PanelRow, ToggleControl } from '@wordpress/components';

import { ColorsControl, Label } from '../../../../../Components';
import { BControlPro } from '../../../../../Components/Pro';

import { buttonContentTypes } from '../../../utils/options';

const Buttons = ({ attributes, setAttributes, updateObj, premiumProps }) => {
	const { buttons = {}, isDoneBtn, doneBtnLink } = attributes;
	const { back = { content: 'text', text: 'Back', colors: { color: '#1a73e8', bg: '#fff' } }, next = { content: 'text', text: 'Next', colors: { color: '#fff', bg: '#1a73e8' } }, done = { text: 'Done', linkTab: '_self', colors: { color: '#fff', bg: '#1e8e3e' } } } = buttons;

	return <PanelBody className='bPlPanelBody' title={__('Buttons', 'stepped-content')} initialOpen={false}>
		<BControlPro label={__('Back Content Type', 'stepped-content')} labelPosition='left' value={back.content} onChange={val => updateObj('buttons', 'back', val, 'content')} options={buttonContentTypes} {...premiumProps} Component={SelectControl} />

		{'text' === back.content && <PanelRow className='mt20'>
			<Label className=''>{__('Back Text', 'stepped-content')}</Label>
			<BControlPro value={back.text} onChange={val => updateObj('buttons', 'back', val, 'text')}{...premiumProps} Component={TextControl} />
		</PanelRow>}

		<ColorsControl className='mt20' value={back.colors} onChange={val => updateObj('buttons', 'back', val, 'colors')} defaults={{ color: '#1a73e8', bg: '#fff' }} />


		<BControlPro className='mt30' label={__('Next Content Type', 'stepped-content')} labelPosition='left' value={next.content} onChange={val => updateObj('buttons', 'next', val, 'content')} options={buttonContentTypes} {...premiumProps} Component={SelectControl} />

		{'text' === next.content && <PanelRow className='mt20'>
			<Label className=''>{__('Next Text', 'stepped-content')}</Label>
			<BControlPro value={next.text} onChange={val => updateObj('buttons', 'next', val, 'text')} {...premiumProps} Component={TextControl} />
		</PanelRow>}

		<ColorsControl className='mt20' value={next.colors} onChange={val => updateObj('buttons', 'next', val, 'colors')} defaults={{ color: '#1a73e8', bg: '#fff' }} />


		<ToggleControl className='mt30' label={__('Show Done Button', 'stepped-content')} checked={isDoneBtn} onChange={val => setAttributes({ isDoneBtn: val })} />

		<PanelRow className='mt20'>
			<Label className=''>{__('Done Text', 'stepped-content')}</Label>
			<BControlPro value={done.text} onChange={val => updateObj('buttons', 'done', val, 'text')} {...premiumProps} Component={TextControl} />
		</PanelRow>

		<TextControl className='mt20' label={__('Done Button Link:', 'stepped-content')} value={doneBtnLink} onChange={val => setAttributes({ doneBtnLink: val })} />

		<BControlPro className='mt10' label={__('Open link in new tab', 'stepped-content')} checked={'_blank' === done.linkTab} onChange={val => updateObj('buttons', 'done', val ? '_blank' : '_self', 'linkTab')} {...premiumProps} Component={ToggleControl} />

		<ColorsControl className='mt20' value={done.colors} onChange={val => updateObj('buttons', 'done', val, 'colors')} defaults={{ color: '#1a73e8', bg: '#fff' }} />
	</PanelBody>
}
export default Buttons;