import { useEffect } from 'react';

import { useWPAjax, useWPOptionQuery } from '../../../react-utils/hooks';

const usePremiumInEditor = () => {
	const { data: stpUtils } = useWPOptionQuery('stpUtils');
	const { data = null, refetch, isLoading = true } = useWPAjax('stpPipeChecker', { _wpnonce: stpUtils?.nonce }, true);
	const isPremium = (!isLoading && data?.isPipe) || false;

	useEffect(() => {
		refetch();
	}, [stpUtils]);

	return { isPremium, isLoading };
};
export default usePremiumInEditor;