import { useState } from 'react';
import { __ } from '@wordpress/i18n';
import { InspectorControls } from '@wordpress/block-editor';
import { PanelBody, TextControl, ToggleControl, SelectControl, __experimentalUnitControl as UnitControl } from '@wordpress/components';
import { produce } from 'immer';

import { Label, Background, BColor, ColorControl, HelpPanel } from '../../../../../Components';
import { AboutPro, BControlPro } from '../../../../../Components/Pro';

import { menuThemes } from '../../../utils/options';
import { emUnit, pxUnit, vhUnit } from '../../../../../Components/utils/options';
import Buttons from './Buttons';

const Settings = ({ attributes, setAttributes, isPremium = false }) => {
	const { layout = {}, nav = {}, navBGColor, title, titleColor, remainingTimeColor, stepsBG, menu = {} } = attributes;
	const { display = true } = nav;
	const { theme = 'numberCount', themeColor = '#1a73e8', textColor = '#212121' } = menu;

	const [aboutProOpen, setAboutProOpen] = useState(false);

	const premiumProps = {
		isPremium,
		setOpen: setAboutProOpen
	};

	const updateObj = (obj, key, val, childKey = null) => {
		const newObj = produce(attributes[obj], draft => {
			if (null !== childKey) {
				draft[key][childKey] = val;
			} else {
				draft[key] = val;
			}
		});
		setAttributes({ [obj]: newObj });
	}

	return <>
		<InspectorControls>
			<div className='stpInspectorInfo'>
				Need more block like this? Checkout the bundle  <a href='https://wordpress.org/plugins/b-blocks' target='_blank' rel='noopener noreferrer'>B Blocks</a>
			</div>

			<HelpPanel slug='stepped-content' docsLink='https://bblockswp.com/docs/stepped-content-block' />


			<PanelBody className='bPlPanelBody' title={__('Layout', 'stepped-content')}>
				<UnitControl label={__('Width:', 'bplugins')} labelPosition='left' value={layout.height} onChange={val => setAttributes({ layout: { ...layout, height: val } })} units={[pxUnit(800), vhUnit(100), emUnit(50)]} />
			</PanelBody>


			<PanelBody className='bPlPanelBody' title={__('Navbar', 'stepped-content')}>
				<BControlPro label={__('Show', 'stepped-content')} checked={display} onChange={val => setAttributes({ nav: { ...nav, display: val } })} {...premiumProps} Component={ToggleControl} />

				<BColor label={__('Background Color:', 'stepped-content')} value={navBGColor} onChange={val => setAttributes({ navBGColor: val })} defaultColor='#fff' />

				<Label>{__('Title:', 'stepped-content')}</Label>
				<TextControl value={title} onChange={val => setAttributes({ title: val })} />

				<BColor label={__('Title Color:', 'stepped-content')} value={titleColor} onChange={val => setAttributes({ titleColor: val })} defaultColor='#3c4043' />

				<BColor label={__('Remaining Time Color:', 'stepped-content')} value={remainingTimeColor} onChange={val => setAttributes({ remainingTimeColor: val })} defaultColor='#3c4043' />
			</PanelBody>


			<PanelBody className='bPlPanelBody' title={__('Menu/Sidebar', 'stepped-content')} initialOpen={false}>
				<BControlPro label={__('Theme', 'stepped-content')} labelPosition='left' value={theme} onChange={val => setAttributes({ menu: { ...menu, theme: val } })} options={menuThemes} {...premiumProps} Component={SelectControl} />

				<BColor label={__('Theme Color:', 'stepped-content')} value={themeColor} onChange={val => setAttributes({ menu: { ...menu, themeColor: val } })} defaultColor='#1a73e8' />

				<ColorControl label={__('Text Color:', 'stepped-content')} value={textColor} onChange={val => setAttributes({ menu: { ...menu, textColor: val } })} defaultColor='#212121' />
			</PanelBody>


			<Buttons attributes={attributes} setAttributes={setAttributes} updateObj={updateObj} premiumProps={premiumProps} />


			<PanelBody className='bPlPanelBody' title={__('Steps', 'stepped-content')} initialOpen={false}>
				<Background label={__('Background:', 'stepped-content')} value={stepsBG} onChange={val => setAttributes({ stepsBG: val })} />
			</PanelBody>
		</InspectorControls>


		<AboutPro aboutProOpen={aboutProOpen} setAboutProOpen={setAboutProOpen} link='https://bplugins.com/products/stepped-content/#pricing'>
			<li>&emsp;<strong>{__('Display Navbar: ', 'stepped-content')}</strong>{__('Show/Hide the top Navbar', 'stepped-content')}</li>

			<li>&emsp;<strong>{__('Menu/Sidebar Theme: ', 'stepped-content')}</strong>{__('Change the Sidebar theme.', 'stepped-content')}</li>

			<li>&emsp;<strong>{__('Back Button Content: ', 'stepped-content')}</strong>{__('Apply Icon, Previous Step Title, or Text for the Back button.', 'stepped-content')}</li>

			<li>&emsp;<strong>{__('Back Button Text: ', 'stepped-content')}</strong>{__('Change the Back button text.', 'stepped-content')}</li>

			<li>&emsp;<strong>{__('Next Button Content: ', 'stepped-content')}</strong>{__('Apply Icon, Next Step Title, or Text for the Next button.', 'stepped-content')}</li>

			<li>&emsp;<strong>{__('Next Button Text: ', 'stepped-content')}</strong>{__('Change the Next button text.', 'stepped-content')}</li>

			<li>&emsp;<strong>{__('Done Button Text: ', 'stepped-content')}</strong>{__('Change the Done button text.', 'stepped-content')}</li>

			<li>&emsp;<strong>{__('Done Button Link Target: ', 'stepped-content')}</strong>{__('Open link in a new tab or current tab for the Done button', 'stepped-content')}</li>
		</AboutPro>
	</>
};
export default Settings;